<template>
<div>
  <el-dialog
    :title="dialog.title" 
    :visible.sync="dialog.show"
    :close-on-click-modal='false'
    :close-on-press-escape='false'
    :modal-append-to-body="false"
  >
    <el-form
      :model="form"
      ref="form"
      label-width="100px"
      class="demo-dynamic"
      :rules ="form_rules"
    >
      <el-form-item prop="_id" label="">
        <el-select  v-model="form._id" placeholder="">
          <el-option
            :key="item._id"
            v-for="item of gameList"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop='title' label="">
        <el-input class="input"  v-model="form.title"></el-input>
      </el-form-item>

      <el-form-item
        v-for="(item,value) in form.content"
        :key="value"
        :label="'' + (value+1)"
      >
        <el-input class="input"  v-model="form.content[value]"></el-input>
        <el-button @click.prevent="removeDomain(value)"></el-button>
      </el-form-item>
    </el-form>
    
    <div slot="footer" class="dialog-footer">
      <el-button @click="addDomain"></el-button>
      <el-button @click="dialog.show = false"> </el-button>
      <el-button type="primary" @click='onSubmit("form")'>  </el-button>
    </div>
  </el-dialog>
</div>
</template>
<script>
import {addLog,updateLog} from '../../api/userHT'
  export default {
    data() {
      return {
        form_rules: {
          _id: [
            { required: true, message: '', trigger: 'blur' }
          ],
          title: [{
            required: true, message: '', trigger: 'blur'
          }]
        }
      };
    },
    props: {
      dialog: Object,
      gameList: Array,
      form: Object
    },
    methods: {
      // 
      addDomain() {
        this.form.content.push('');
      },
      // 
      addGameLog() {
        addLog(this.form)
          .then(res => {
            if (res.success) {
              this.$message({
                message: "",
                type: "success"
              });
              this.dialog.show = false
              this.dialog.title = ''
              this.$emit("update");
            }
          })
      },
      // 
      onSubmit() {
        this.form.user_id = this.users.id
        if (!this.form._id) {
          this.$message({
            message: "",
            type: "error"
          });
        } else {
          if (this.dialog.option == 'add') {
            this.addGameLog()
          } else if(this.dialog.option == 'update') {
            this.updateLog()
          }
        }
      },
      // 
      removeDomain(item) {
        this.form.content.splice(item,1)
      },
      
      // 
      updateLog() {
        updateLog(this.form)
          .then(res => {
             if (res.success) {
              this.$message({
                message: "",
                type: "success"
              });
              this.dialog.show = false
              this.dialog.title = ''
              this.$emit("update");
            }
          })
      }
    },
    computed: {
      users() {
        return this.$store.getters.user;
      }
    }
  };
</script>
<style scoped>
.input {
  width: 80%;
}
</style>
