<template>
  <div class="logFund">
    <el-dialog 
      :title="dialog.title" 
      :visible.sync="dialog.show"
      :close-on-click-modal='false'
      :close-on-press-escape='false'
      :modal-append-to-body="false">
      <div class="form">
        <el-form 
          ref="form" 
          :model="form"
          :rules ="form_rules"
          label-width="120px" 
          style="margin:10px;width:auto;"
        >
          <el-form-item prop='name' label=":">
            <el-input type="name" v-model="form.name"></el-input>
          </el-form-item>

          <el-form-item prop='size'  label=":">
            <el-input type="size" v-model="form.size"></el-input>
          </el-form-item>

          <el-form-item prop='versions' label="">
            <el-input type="versions" v-model="form.versions"></el-input>
          </el-form-item>

          <el-form-item prop='date' label=":">
            <el-input type="date" v-model="form.date"></el-input>
          </el-form-item>

          <el-form-item label=":" prop='studio' >
            <el-input type="studio" v-model="form.studio"></el-input>
          </el-form-item>
          <el-form-item label=":" prop='prince'>
            <el-input type="prince" v-model="form.prince"></el-input>
          </el-form-item>
          <el-form-item label=":" prop='declaration'>
            <el-input type="textarea" v-model="form.declaration"></el-input>
          </el-form-item>
          <el-form-item  class="text_right">
              <el-button @click="dialog.show = false"> </el-button>
              <el-button type="primary" @click='onSubmit("form")'>  </el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {upbasic} from '@/api/userHT'
export default {
  name: "logfound",
  props: {
    dialog: Object,
    form: Object
  },
  data() {
    return {
      form_rules: {
        name: [
          { 
            required: true,
            message: "", 
          },
        ],
        size: [
          {
            required: true, 
            message: "",
          },
          {
            pattern: /^([0-9]+)|([0-9]+\.[0-9]+)$/,
            message: 'G'
          }
        ],
        versions: [
          { required: true, message: "ccc", trigger: "blur" }
        ],
        studio: [
          { required: true, message: "", trigger: "blur" }
        ],
        date: [
          { required: true, message: "", trigger: "blur" }
        ],
        prince: [
          { required: true, message: "", trigger: "blur" }
        ],
         declaration: [
          { required: true, message: "", trigger: "blur" }
        ],
      }
    };
  },
  methods: {
    onSubmit(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          // ;
          upbasic(this.form)
            .then(res => {
              if (res) {
                this.$message({
                  message: "",
                  type: "success"
                });
              }
              this.dialog.show = false;
              this.$emit("update");
            });
          }
      });
    }
  }
};
</script>

