<template>
  <div class="logFund">
    <el-dialog 
      :title="dialog.title" 
      :visible.sync="dialog.show"
      :close-on-click-modal='false'
      :close-on-press-escape='false'
      :modal-append-to-body="false">
      <div class="form">
        <el-form 
          ref="form" 
          :model="form"
          :rules="form_rules"
          label-width="120px" 
          style="margin:10px;width:auto;"
        >
          <el-form-item prop='name' label=":">
            <el-input type="name" v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item prop='email'  label=":">
            <el-input type="email" v-model="form.email"></el-input>
          </el-form-item>

          <el-form-item prop='password' label="">
            <el-input type="password" v-model="form.password"></el-input>
          </el-form-item>

          <el-form-item prop='phone' label=":">
            <el-input type="phone" v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item prop='grade' label=":">
            <el-input type="grade" v-model="form.grade"></el-input>
          </el-form-item>
          <el-form-item  class="text_right">
              <el-button @click="dialog.show = false"> </el-button>
              <el-button type="primary" @click='onSubmit("form")'>  </el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {update} from '@/api/userHT'
export default {
  name: "logfound",
  props: {
    dialog: Object,
    form: Object
  },
  data() {
    return {
      form_rules: {
        name: [
          { required: true, message: "", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "", trigger: "blur" }
        ],
        grade: [
          { required: true, message: "", trigger: "blur" }
        ],
      }
    };
  },
  methods: {
    onSubmit(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          // ;
          update(this.form)
            .then(res => {
              if (res.data.success) {
                this.$message({
                  message: res.data.msg,
                  type: "success"
                });
              }
              this.dialog.show = false;
              this.$emit("update");
            });
          }
      });
    }
  }
};
</script>

