<template>
  <div id="wangeditor">
    <div ref="editorElem" style="text-align:left;height:450px;"></div>
  </div>
</template>
<script>
import E from "wangeditor";
import {articlesimg} from '../api/url'
export default {
  name: "editorElem",
  data() {
    return {
      editor: null,
      editorContent: ""
    };
  },
  props: ["catchData", "content"], // 
  watch: {
    content() {
      this.editor.txt.html(this.content);
    }
  },
  mounted() {
    this.editor = new E(this.$refs.editorElem);
    this.editor.customConfig.onchange = html => {
      this.editorContent = html;
      this.catchData(this.editorContent); // htmlcatchData
    };
    this.editor.customConfig.uploadImgServer = articlesimg;
    this.editor.customConfig.uploadFileName = "screen";
    this.editor.customConfig.menus = [
      // 
      "head", // 
      "bold", // 
      "fontSize", // 
      "fontName", // 
      "italic", // 
      "underline", // 
      "strikeThrough", // 
      "foreColor", // 
      "backColor", // 
      "link", // 
      "list", // 
      "justify", // 
      "quote", // 
      "emoticon", // 
      "image", // 
      "table", // 
      "code", // 
      "undo", // 
      "redo" // 
    ];
    // 
    this.editor.customConfig.uploadImgHooks = {
      // before: function(xhr, editor, files) {
      //   // 
      //   // xhr  XMLHttpRequst editor files 
      //   //  {prevent: true, msg: 'xxxx'} 
      //   // return {
      //   //     prevent: true,
      //   //     msg: ''
      //   // }
      // },
      success: function(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        this.imgUrl = Object.values(result[0]).toString();
      },
      // fail: function(xhr, editor, result) {
      //   console.log('cccccc')
      //   // 
      //   // xhr  XMLHttpRequst editor result 
      // },
      // error: function(xhr, editor) {
      //   console.log('dddd')
      //   // 
      //   // xhr  XMLHttpRequst editor 
      // },
      // timeout: function(xhr, editor) {
      //   // 
      //   // xhr  XMLHttpRequst editor 
      // },

      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: function(insertImg, result) {
        // 
        // insertImg editor result 

        //  {url:'....'} 
        // console.log(result)
        let url = Object.values(result[0]); // result.data
        // JSON.stringify(url); // JSON
        insertImg(url);
        // result  JSON 
      }
    };

    this.editor.create(); // 
    if (!this.content) {
      this.editor.txt.html("");
    }
  }
};
</script>
