<template>
<div>
  <div class="updateLog">
  <div class="update-header">
    <!--  -->
    <el-select style="margin:0 5px;" v-model="gameId" clearable placeholder="" @change="clearName">
      <el-option
        v-for="item in datas"
        :key="item._id"
        :label="item.name"
        :value="item._id">
      </el-option>
    </el-select>
    <!-- -->
    <el-select v-if="gameLog" v-model="logId" placeholder="">
      <el-option
        v-for="item in this.gameLog.log"
        :key="item._id"
        :label="item.title"
        :value="item._id">
      </el-option>
    </el-select>
    <el-button style="margin-left:10px;"  size="medium " type="success" :disabled="!logId?true: false" @click="changeLog"></el-button>
    <el-button style="position: absolute;right:30px;"   size="medium"  type="primary" @click="addVersion"></el-button>
  </div>

  <!--  -->
  <div class="updateTable">
    <div v-if="!this.logList" class="nullData"></div>
    <el-table
      v-else
      :data="logList.content"
      border
      style="width: 100%">
      <el-table-column
        type="index"
        width="50"
        align="center"
        label=""
      >
      </el-table-column>
      <el-table-column
        label=""
        align="center"
        width="880">
        <template slot-scope="scope">
          <span style="margin-left: 10px;color: #333;">{{ scope.row }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="180"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>

  <div  v-if="this.logList"  style="text-align:right;margin:20px 100px; 0 0">
    <!-- <el-button type="primary" @click="update"></el-button> -->
    <el-button type="primary" @click="updateLog"></el-button>
    <el-button type="danger" @click="removeVersion"></el-button>
    <!-- <el-button type="primary" @click="removeVersion"></el-button> -->
  </div>
  <AddLog :form="form" :dialog="dialog" :gameList="datas" @update="getdata"></AddLog>
</div>
</div>
</template>

<script>
import AddLog from '../components/popout/AddLog.vue';
import {getLog,deleteLog,updateLog} from '../api/userHT'
  export default {
    components: {
      AddLog
    },
    data() {
      return {
        // 
        datas: [],
        // id
        gameId: '',
        // id
        logId: '',
        // 
        logList: [],
        dialog: {
          show: false,
          title: "",
          option: ''
        },
        // 
        form: {
          _id: '',
          title: '',
          content: []
        },
      }
    },
    methods: {
      // 
      getdata() {
        getLog(this.user.id)
          .then(res => {
            if (res.data.result.length!=0) {
              this.datas = res.data.result;
              if (!res.data.result){
                return;
              }
              if (res.data.result[0]) {
                this.logList = res.data.result[0].log[0]
                this.gameId = res.data.result[0]._id
              } else {
                this.gameId = ''
              }
              if (res.data.result[0].log[0]) {
                this.logId = res.data.result[0].log[0]._id
              } else {
                this.logId = ''
              }
              this.form = {
                _id: '',
                title: '',
                content: []
              }
            }
          })
      },
      // 
      clearName() {
        this.logId = ''
      },
      // 
      changeLog() {
        let logdata = {}
         this.gameLog.log.forEach(element => {
            if (element._id==this.logId) {
              logdata = element
            }
          })
        this.logList = logdata
      },
      // 
      addVersion() {
        this.dialog = {
          show: true,
          title: '',
          option: 'add'
        };
      },
      updateLog() {
        this.dialog = {
          show: true,
          title: '',
          option: 'update'
        };
        this.form = this.logList
        this.form.game_id = this.gameId
      },
      //
      removeVersion() {
        let data = {
         _id: this.gameId,
         user_id: this.user.id,
         msg_id: this.logId
        }
        deleteLog(data)
          .then(res => {
            if (res.success) {
              this.$message({
                message: '',
                type: "success"
              });
              this.getdata()
            }
          })
      },
      // 
      handleDelete(res) {
        this.form = this.logList
        this.form.game_id = this.gameId
        this.form.user_id = this.user.id
        this.form.content.splice(res,1)
        updateLog(this.form)
          .then(res => {
             if (res.success) {
              this.$message({
                message: "",
                type: "success"
              });
            }
          })
      },

      
    },
    created() {
      // 
      this.getdata();
    },
    computed: {
      // 
      user(){
        return this.$store.getters.user;
      },
      // 
      gameLog() {
        let gameLog = {}
        this.datas.forEach(element => {
          if (element._id==this.gameId) {
            gameLog = element
          }
        });
        return gameLog
      },
    }
  }
</script>
<style scoped>
.updateLog {
  columns: red;
  padding: 10px;
  margin-top: 10px;
}
.title {
  text-align: center;
  font-weight: bold;
  font-size: 40px;
}
.nullData {
  color: #cccccc;
  font-size: 30px;
  text-align: center;
  padding: 40px 0;
}

.updateTable {
  margin-top: 5px;
}
</style>
