<template>
<!--  -->
  <div class="register">
    <div class="form_container">
      <div class="manage_tip">
        <span class="title"></span>
        <el-form
          :model="register"
          :rules="rules"
          class="registerForm"
          ref="registerForm"
          label-width="80px"
          :validate-on-rule-change="false"
        >
          <el-form-item label="" prop="name">
            <el-input type="name" v-model="register.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone" ref="registerPhone">
            <el-input type="name" v-model="register.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="code" >
            <el-input style="width:110px;float:left;" type="code" v-model="register.code" placeholder=""></el-input>
            <el-button :disabled="isDisabled" style="float:right;margin-left:0;" @click="sencode('registerForm')">{{buttonName}}</el-button>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input
            type="password"
            v-model="register.password"
            placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password2">
            <el-input
              type="password"
              v-model="register.password2" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
            class="submit_btn"
            type="primary"
            @click="submitForm('registerForm')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
// api
import { register,sendcode } from '../api/userHT'
export default {
  name: 'register',
  data() {
    // 
    var checkPassword = (rule, value, callback) => {
      if (value !== this.register.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      register: {
        name: '',
        password: '',
        password2: '',
        email: '',
        phone: '',
        code: ''
      },
      rules: {
        // 
        email: [{
          required: true, 
          message: '',
          trigger: 'blur'
        },
        {
          pattern:/^\d{8,10}@qq.com$/,
          message: '',
          trigger: 'blur'
        }],
        // 
        name: [{
          required:true,  //
          message: '', //
          trigger: 'blur' //     blur'
        }, {  //
          min: 2,
          max: 30,
          message: '230',
          trigger: 'blur'
        }],
        phone: [{
          required:true, 
          message: ''
        }, {
          min: 11,
          max: 11,
          message: '11',
          trigger: 'blur'
        }, {
          pattern: /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/,
          message: '',
          trigger: 'blur'
        }],
        code: [{
          required:true, 
          message: '',
          trigger: 'blur'
        }, {
          min: 6,
          max: 6,
          message: '6',
          trigger: 'blur'
        }],
        // 
        password: [
        {
          required:true,
          message: '',
          trigger: 'blur'
        },
        {
          min: 8,
          max: 30,
          message: "820",
          trigger: 'blur'
        },
        {
          pattern:/^.*(?=.{8,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*?. ]).*$/,
          message: '',
          trigger: 'blur'
        }],
        // 
        password2: [{
          required:true,
          message: '',
          trigger: 'blur'
        },{
          validator: checkPassword,
          trigger: 'blur'
        }]
      },
      buttonName: "",
      isDisabled: false,
      time: 60
    }
  },
  methods: {
    // ,
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          register(this.register)
          .then(res => {
            if (res.success) {
              this.$message({
                message: res.msg,
                type: 'success'
              })
              this.$router.push("/login")
              return;
            } else {
              this.$message({
                message: res.msg,
                type: 'error'
              })
            }
          })
        } else {
          return false;
        }
      });
    },
    // 
    sencode(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var data = {
            phone: this.register.phone
          }
          sendcode(data)
            .then(res => {
              if(res.success) {
                let me = this;
                me.isDisabled = true;
                let interval = window.setInterval(function() {
                  me.buttonName =   me.time + '';
                  --me.time;
                  if(me.time < 0) {
                    me.buttonName = "";
                    me.time = 60;
                    me.isDisabled = false;
                    window.clearInterval(interval);
                  }
                }, 1000);
                this.$message({
                  message: res.msg,
                  type: 'success'
                })
              } else {
                this.$message({
                  message: res.msg,
                  type: 'error'
                })
              }
          })
        }
      })
    }
  }
}
</script>
<style scoped>
  .register {
  position: relative;
  width: 100%;
  height: 100%;
  background: url(../assets/bg.jpg) no-repeat center center;
  background-size: 100% 100%;
}
.form_container {
  width: 400px;
  height: 210px;
  position: absolute;
  top: 10%;
  left: 34%;
  padding: 25px;
  border-radius: 5px;
  text-align: center;
}
.form_container .manage_tip .title {
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 26px;
  color: #fff;
}
.registerForm {
  margin-top: 20px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0px 5px 10px #cccc;
}

.submit_btn {
  width: 100%;
}

</style>
